<template>
  <section class="main">
    <loader v-if="!loaded" />
    <aside v-if="loaded" class="sidebar">
      <div class="sidebar__my">
        <div class="header">
          <template v-if="isDeviceLinked">
            <span class="header__active">{{ this.$t("ACTIVE_DEVICE") }}</span>
            <img class="header__img" :src="currentDevice.icon" />
            <div class="header__desc">
              <span class="header__title">{{ currentDevice.name }}</span>
              <span class="header__device">{{ currentDevice.title }})</span>
              <ul class="header__list">
                <template v-if="currentDevice.plus">
                  <li class="header__divider"></li>
                  <li class="header__status header__status_pro">Plus</li>
                </template>
              </ul>
            </div>
          </template>

          <template v-else>
            <img class="header__img" src="@/assets/icon-nodevice.svg" />
            <div class="header__desc">
              <span class="header__error-status g-status g-status_error">{{
                $t("NO_DEVICE_LINKED")
              }}</span>
              <router-link to="/link" class="g-button">{{
                $t("LINK_YOUR_DEVICE")
              }}</router-link>
            </div>
          </template>
        </div>
        <nav>
          <ul class="nav" :class="{ nav_disabled: !isDeviceLinked }">
            <li class="nav__item" v-for="item in nav" :key="item.title">
              <a v-if="item.external" class="nav__link" :href="item.link" :target="item.target">
                {{ item.title }}
              </a>
              <router-link v-else class="nav__link" :to="'/my' + item.link">{{
                item.title
              }}</router-link>
            </li>
          </ul>
        </nav>
      </div>
    </aside>
    <slot v-if="loaded">
      <router-view />
    </slot>
  </section>
</template>

<script>
import Loader from '@/components/Loader';

export default {
  components: { Loader },
  computed: {
    nav() {
      return [
        {
          title: this.$t('MY_DEVICES'),
          link: '/devices',
        },
        {
          title: this.$t('MY_APPS_TITLE'),
          link: '/apps',
        },
        {
          title: this.$t('MY_INSTALLATION_HISTORY'),
          link: '/installation-history',
        },
        {
          title: this.$t('MY_DYLIBS'),
          link: '/dylibs',
        },
        {
          title: this.$t('MY_REPOSITORIES'),
          link: '/repos',
        },
        /* {
          title: this.$t('CHECK FOR UPDATES'),
          link: '/updates',
        }, */
        {
          title: this.$t('CONFIGURE_FEATURES'),
          link: '/configure',
        },
        {
          title: this.$t('VIEW_PLUS_STATUS'),
          link: '/plus',
        },
        {
          title: this.$t('P2P_SIGNING_MARKETPLACE'),
          link: '/p2p-certificates',
        },
        {
          title: this.$t('VIEW DEVICE STATUS'),
          link: '/status',
        },
        {
          title: this.$t('CHANGE_EMAIL'),
          link: '/change-email',
        },
        {
          title: this.$t('TROUBLESHOOTING'),
          link: 'https://rtfm.dbservices.to/#/troubleshooting/app-installations',
          external: true,
          target: '_blank',
        },
        {
          title: this.$t('UNLINK THIS DEVICE'),
          link: '/unlink',
        },
      ];
    },
    loaded() {
      const { configLoaded, devicesLoaded } = this.$store.getters;
      return configLoaded && devicesLoaded;
    },
    isDeviceLinked() {
      return this.$store.getters.isDeviceLinked;
    },
    currentDevice() {
      return this.$store.getters.devices
        .find(device => device.lt === this.$store.getters.lt) || {};
    },
  },
};
</script>

