<template>
  <v-layout>
    <section class="repos" :class="{repos_loading: gettingRepos}">
      <h1 class="intro">
        {{ $t('PUBLIC_REPOS') }}
      </h1>
      <div v-if="gettingRepos" class="loader-wrapper">
        <loader />
      </div>
      <template v-else>
        <div class="items-wrapper">
          <p class="instruction" v-if="repos.length">
            {{ $t("MY_REPOS_MOBILE_INSTRUCTION") }}
          </p>
          <div class="items">
            <div v-if="!repos.length">
              No public repositories found
            </div>
            <div
              class="item item_active"
              v-for="repo in repos"
              :class="{
                item_disabled: repoRemovingId === repo.id,
              }"
              :key="repo.id"
              @click="browseRepoMobile(repo.id, $event)"
            >
              <div class="item__desc">
                <span class="item__title">
                  {{ repo.name }}
                </span>
                <span class="item__info">
                  {{ $t('STATUS_IS_X', {status: repo.status_translated}) }}
                  <br/>
                  {{ $t('APPS_N', {apps: repo.total_apps}) }}
                  <br/>
                  {{ $t('LAST_CHECKED_AT',
                    {date: new Date(repo.last_checked_at * 1000).toLocaleString()}) }}
                </span>
              </div>
              <div class="item__switch">
                <router-link class="item__button" :to="`/repos/${repo.id}`">
                  {{ $t("BROWSE") }}
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <div class="link">
          <html-with-links
            tag="p"
            class="link__text"
            :html="$t('SEARCHING_FOR_PRIVATE_REPOS', {href: '/my/repos'})"
          />
          <p class="link__text">
            {{ $t("MY_REPOSITORIES_SIDEBAR") }}
          </p>
        </div>
      </template>
    </section>
  </v-layout>
</template>

<script>
import { mapGetters } from 'vuex';
import isItselfLinkable from '@/utils/isItselfLinkable';
import Loader from '@/components/Loader.vue';
import VLayout from '@/views/My/index.vue';
import HtmlWithLinks from '@/components/HtmlWithLinks';

export default {
  components: {
    Loader, VLayout, HtmlWithLinks,
  },
  data() {
    return {
      origin: window.location.origin,
      code: '',
      isMobile: isItselfLinkable(),
      deviceRemoving: null,
      repoRemovingId: null,
      gettingRepos: true,
      repos: [],
    };
  },
  computed: {
    title() {
      return this.$t('PUBLIC_REPOS');
    },
    ...mapGetters(['isDeviceLinked']),
  },
  methods: {
    tryAddRepository() {
      if (!this.isDeviceLinked) {
        this.$confirm(this.$t('LINK_TO_ADD_REPO')).then(() => {
          this.$router.push('/link');
        });
        return;
      }

      this.$prompt(this.$t('ENTER_REPOSITORY_URL'), '', { validate: this.validateRepoURL })
        .then((url) => {
          this.$router.push(`/repos/import?url=${encodeURIComponent(url)}`);
        });
    },
    validateRepoURL(url) {
      try {
        // eslint-disable-next-line no-new
        new URL(url);

        return true;
      } catch {
        this.$notify({
          type: 'error',
          text: this.$t('PROVIDE_VALID_URL'),
        });

        return false;
      }
    },
    browseRepo(id) {
      this.$router.push(`/repos/${id}`);
    },
    removeRepo(id) {
      this.repoRemovingId = id;
      this.$api.deleteRepo(id)
        .then(() => {
          const idx = this.repos.findIndex(repo => repo.id === id);
          this.repos.splice(idx, 1);
        })
        .finally(() => {
          this.repoRemovingId = null;
        });
    },
    browseRepoMobile(id) {
      if (window.innerWidth >= 1250) {
        return;
      }
      this.$router.push(`/repos/${id}`);
    },
    getRepos() {
      this.gettingRepos = true;
      return this.$api.getRepos(1)
        .then((response) => {
          this.repos = response;
        })
        .finally(() => {
          this.gettingRepos = false;
        });
    },
  },
  created() {
    this.getRepos();
  },
};
</script>

